/**
 * 
 * @param {String} imgUrl  
 * @param {Object} self	
 * @param {Object} options 
 * 		width: ()
 * 		height: 
 * 		pixels: 
 * 		quality: 0.8
 * 		type: base64jpg
 * 		base64: base64true(H5)
 * @return {Promise}
 * 		reject
 * 			code
 * 				-1: 
 * 				-2: (h50)
 * 				-3: canvas()
 * 				-4: base64()
 */

// 
const calcImageSize = (res, pixels) => {
	let imgW, imgH
	imgW = res.width
	imgH = res.height
	
	let ratio
	if((ratio = imgW * imgH / pixels) > 1) {
		ratio = Math.sqrt(ratio)
		imgW = parseInt(imgW / ratio)
		imgH = parseInt(imgH / ratio)
	} else {
		ratio = 1
	}
	
	return { imgW, imgH }
}

const urlTobase64 = (url, type) => {
	return new Promise((resolve, reject) => {
		uni.getFileSystemManager().readFile({
			filePath: url,
			encoding: 'base64',
			success: res => {
				let base64 = res.data
				base64 = `data:image/${type};base64,${base64}`
				resolve(base64)
			}
		})
	})
}

const compress = (imgUrl, slef, options={}) => {
	/***************  ***************/
	const MAX_PIXELS = 2000000	//  *  
	const MAX_QUALITY = 0.8	// 
	const IMG_TYPE = 'jpg'
	const CANVAS_ID = 'compress_canvas'
	const BASE_64 = false
	
	return new Promise((resolve, reject) => {
		uni.getImageInfo({
			src: imgUrl,
			success: res => {
				let pixels = options.pixels || MAX_PIXELS
				let quality = options.quality || MAX_QUALITY
				let type = options.type || IMG_TYPE
				let canvasId = options.canvasId || CANVAS_ID
				let isBase64 = options.base64 || BASE_64
				
				let { imgW, imgH } = calcImageSize(res, pixels)
				let w = options.width || imgW
				let h = options.height || imgH



				

				type = type == 'png' ? 'png' : 'jpg',

				console.log(`%c : ${w} %c : ${h} %c : ${w * h} %c : ${quality} %c : ${type}`, 'color:#f00', 'background-color:#f60;color:#fff', 'color:#F00', 'background-color:#f60;color:#fff', 'color:#F00')
				

















				
				
				// h5

				slef.height = h
				slef.width = w
				
				slef.$nextTick(function() {
					let canvas = null
					if(!canvas) {
						canvas = uni.createCanvasContext(canvasId, slef)
					}
					canvas.drawImage(res.path, 0, 0, w, h)
					canvas.draw()
					setTimeout(() => {
						uni.canvasToTempFilePath({
							canvasId: canvasId,
							x: 0,
							y: 0,
							width: w,
							height: h,
							destWidth: w,
							destHeight: h,
							fileType: type,
							quality: quality,
							success: file => {
								if(isBase64) {
									urlTobase64(file.tempFilePath, type)
										.then(res => {
											canvas = null
											resolve(res)
										})
										.catch(e => {
											reject({
												code: -4,
												msg: 'base64',
												data: e
											})
										})
								} else {
									resolve(file.tempFilePath)
								}
							},
							fail: e => {
								reject({
									code: -3,
									msg: 'canvas',
									data: e
								})
							}
						}, slef)
					}, 500)
				})

			}
		})
	})
}

export default compress
